export default {
	data() {
		return {
			SessionKey: '',
			OpenId: '',
			nickName: null,
			avatarUrl: null,
			isCanUse: uni.getStorageSync('isCanUse') || true //默认为true
		};
	},
	methods: {
		//第一授权获取用户信息===》按钮触发
		wxGetUserInfo() {
			// console.log(111111111)
			let _this = this;
			uni.getUserInfo({
				provider: 'weixin',
				success: function(infoRes) {
					console.log(infoRes)
					let nickName = infoRes.userInfo.nickName; //昵称
					let avatarUrl = infoRes.userInfo.avatarUrl; //头像
					try {
						uni.setStorageSync('isCanUse', false); //记录是否第一次授权  false:表示不是第一次授权
						_this.updateUserInfo(); //调用更新信息方法
					} catch (e) {}
				},
				fail(res) {}
			});
		},
		//登录
		login() {
			let _this = this;
			uni.showLoading({
				title: '登录中...'
			});

			// 1.wx获取登录用户code
			uni.login({
				provider: 'weixin',
				success: function(loginRes) {
					console.log(loginRes)
					let code = loginRes.code;
					if (!_this.isCanUse) {
						//非第一次授权获取用户信息
						uni.getUserInfo({
							provider: 'weixin',
							success: function(infoRes) {
								//获取用户信息后向调用信息更新方法
								let nickName = infoRes.userInfo.nickName; //昵称
								let avatarUrl = infoRes.userInfo.avatarUrl; //头像
								_this.updateUserInfo(); //调用更新信息方法
							}
						});
					}

					//2.将用户登录code传递到后台置换用户SessionKey、OpenId等信息
					uni.request({
						url: 'http://zm.dangjian.link/phone/rediectH5',
						data: {
							code: code,
						},
						method: 'GET',
						header: {
							'content-type': 'application/json'
						},
						success: (res) => {
							console.log(res)
							//openId、或SessionKdy存储//隐藏loading
							uni.hideLoading();
							if(res.success){
								uni.navigateTo({
									url:"/pages/home_page/home_page"
								})
							}
						}
					});
				},
			});
		},
	},
	onLoad() { //默认加载
		this.login();
	}
}
